a {
  cursor: pointer;
}
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.6);
  z-index: 50; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer;
}

.jumbotron {
  padding: 2rem;
}

/*override rc-switch styles*/
.rc-switch {
  width: 80px !important;
  background-color: rgba(5, 255, 15, 0.5) !important;
}
.rc-switch-checked:after {
  left: 60px !important;
}
.rc-switch-checked {
  background-color: rgba(250, 5, 15, 0.5) !important;
}

/* react table */
.ReactTable .rt-td {
  padding: 0;
  text-align: end;
}
.close-btn {
  font-size: 2em;
  cursor: pointer;
  line-height: 1;
}

.notification {
  min-width: 250px; /* Set a default minimum width */
  max-width: 400px;
  margin-left: -125px; /* Divide value of min-width by 2 */
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 30;
  right: 20px;
  top: 50px;
}

.notification.fadein {
  -webkit-animation: fadein 0.5s;
          animation: fadein 0.5s;
}
.notification.fadeout {
  -webkit-animation: fadeout 0.5s;
          animation: fadeout 0.5s;
}

@-webkit-keyframes fadein {
  from {top: 0; opacity: 0;}
  to {top: 50px; opacity: 1;}
}

@keyframes fadein {
  from {top: 0; opacity: 0;}
  to {top: 50px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {top: 50px; opacity: 1;}
  to {top: 0; opacity: 0;}
}

@keyframes fadeout {
  from {top: 50px; opacity: 1;}
  to {top: 0; opacity: 0;}
}
